	module.exports = {"data":{"markdownRemark":{"html":"<p>A few pieces of armor have been tweaked.</p>\n<p>Specifically, the mithril armor from the cold-drake has been standardized,\nthe ancient mithril breastplate has been completely remodeled (and re-named),\nand the light silver helmet has been renamed (but not remodeled).</p>\n<p>Additionally, etched leather is now marginally heavier than before, and\nprovides slightly worse protection.</p>\n<p>It also came to our attention that the intended balancing malus on the\nlong bone sceptre was not functioning properly. This has been fixed.</p>\n<p>Send any complaints to Blent.</p>","frontmatter":{"title":"News 1596","date":"October 09, 2017","author":"Blent","desc":"Tweaks to Armor"}}},"pathContext":{"path":"/news/1596"}}

